100%, transparent 0),\n        radial-gradient(circle 10px, #000 100%, transparent 0);\n    background-position: 0em -18em, 0em 18em, 18em 0em, -18em 0em,\n        13em -13em, -13em -13em, 13em 13em, -13em 13em;\n    background-repeat: no-repeat;\n    font-size: 0.5px;\n    border-radius: 50%;\n    animation: blast 1s ease-in infinite;\n}\n\n.loader::after {\n    font-size: 1px;\n    background: #000;\n    animation: bounce 1s ease-in infinite;\n}\n\n@keyframes bounce {\n\n    0%,\n    100% {\n        font-size: 0.75px\n    }\n\n    50% {\n        font-size: 1.5px\n    }\n}\n\n@keyframes blast {\n\n    0%,\n    40% {\n        font-size: 0.5px;\n    }\n\n    70% {\n        opacity: 1;\n        font-size: 4px;\n    }\n\n    100% {\n        font-size: 6px;\n        opacity: 0;\n    }\n}\n\n.weather-div {\n    display: grid;\n    grid-template-columns: 150px 1fr;\n    width: 100%;\n    gap: 1rem 0;\n    padding-inline: 0.5rem;\n}\n\n.location-time-div {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    padding-top: 5rem;\n    text-align: center;\n    grid-column: span 2;\n    grid-row: 1;\n}\n\n.location-time-div .main {\n    height: 55px;\n    font-size: 50px;\n}\n\n.location-time-div .sub {\n    font-size: 24px;\n}\n\n.location-time-div .time {\n    font-size: 20px;\n}\n\n.weather-div .icon {\n    width: 100px;\n    grid-row: 2;\n    align-self: center;\n    justify-self: end;\n    margin-right: 1rem;\n}\n\n.temp-scale-div {\n    display: flex;\n    grid-column: span 2;\n    grid-row: 2;\n    align-self: center;\n}\n\n.temp-scale-div .temp-div {\n    display: flex;\n    font-size: 45px;\n}\n\n.temp-div h2:not(.active) {\n    display: none;\n}\n\n.temp-scale-div .scale-div {\n    color: #696969;\n    display: flex;\n    align-items: center;\n}\n\n.scale-div button {\n    flex-grow: 0;\n    border: none;\n    font-size: 24px;\n    background-color: transparent;\n    color: inherit;\n    cursor: pointer;\n    padding: 3px;\n}\n\n.scale-div .active {\n    font-weight: bold;\n    color: #000;\n}\n\n.condition-desc-div {\n    text-align: center;\n    grid-column: span 2;\n}\n\n.condition-desc-div .condition {\n    font-size: 24px;\n}\n\n.condition-desc-div .description {\n    font-size: 18px;\n}\n\n.carousel-div {\n    width: 95%;\n    flex-grow: 0;\n    grid-column: span 2;\n    justify-self: center;\n    overflow: hidden;\n}\n\n.slides-div {\n    display: flex;\n    overflow-x: auto;\n    scroll-snap-type: x mandatory;\n    scroll-behavior: smooth;\n    -webkit-overflow-scrolling: touch;\n    height: 270px;\n}\n\n.slides-div::-webkit-scrollbar {\n    display: none;\n}\n\n.slides-div>div {\n    scroll-snap-align: start;\n    flex-shrink: 0;\n    position: relative;\n    width: 100%;\n    margin-inline: 1rem;\n}\n\n.slides-div .weather-properties-div {\n    display: grid;\n    grid-template: repeat(2, 1fr) / repeat(2, 1fr);\n    place-items: center;\n    text-align: center;\n    overflow-y: hidden;\n    margin-top: 8px;\n    gap: 0 12px;\n}\n\n.weather-properties-div .square-div:nth-child(1) {\n    justify-self: end;\n}\n\n.weather-properties-div .square-div:nth-child(2) {\n    justify-self: start;\n}\n\n.weather-properties-div .square-div:nth-child(3) {\n    justify-self: end;\n}\n\n.weather-properties-div .square-div:nth-child(4) {\n    justify-self: start;\n}\n\n.weather-properties-div .square-div {\n    width: 130px;\n    height: 120px;\n    background-color: #DFE1E5;\n    border-radius: 24px;\n    padding: 1rem;\n    display: flex;\n    flex-direction: column;\n}\n\n.square-div .square-content,\n.square-content span {\n    display: flex;\n    justify-content: center;\n    font-size: 32px;\n}\n\n.square-div .square-title {\n    font-size: 16px;\n}\n\n.square-content {\n    margin-top: 5px;\n}\n\n.square-content span:not(.active) {\n    display: none;\n}\n\n.square-content i {\n    font-style: normal;\n    font-size: 20px;\n}\n\n.daily-div {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 1rem;\n    overflow-y: scroll;\n    scroll-snap-type: y mandatory;\n    scroll-behavior: smooth;\n    -webkit-overflow-scrolling: touch;\n}\n\n.daily-div::-webkit-scrollbar {\n    height: 10px;\n    width: 5px;\n}\n\n.daily-div::-webkit-scrollbar-thumb {\n    background: #696969;\n    border-radius: 10px;\n}\n\n.daily-div::-webkit-scrollbar-track {\n    background: transparent;\n}\n\n.daily-div .day-div {\n    scroll-snap-align: start;\n    height: 75px;\n    display: flex;\n    padding: 1rem;\n    align-items: center;\n    background-color: #DFE1E5;\n    border-radius: 24px;\n    text-align: center;\n}\n\n.day-div>* {\n    width: 105px;\n}\n\n.day-div .day-icon {\n    height: 55px;\n}\n\n.day-div .day-temp-div {\n    display: flex;\n    flex-direction: column;\n    font-size: 18px;\n}\n\n.day-temp-div span:not(.active) {\n    display: none;\n}\n\n.day-temp-div i {\n    font-style: normal;\n}\n\n.max-temp {\n    font-weight: bold;\n}\n\n.indicator-div {\n    margin-top: 5px;\n    display: flex;\n    justify-content: center;\n    gap: 8px;\n    height: 15px;\n}\n\n.indicator-div .dot-div {\n    width: 15px;\n    height: 15px;\n    border-radius: 50%;\n    background-color: #e5e5e5;\n}\n\n.dot-div.active {\n    background-color: #666666;\n}\n\n@media (width > 320px) {\n    .weather-div {\n        grid-template-columns: 1fr 1fr;\n    }\n}\n\n@media (width >=800px) {\n    .weather-div {\n        gap: 0;\n    }\n\n    .location-time-div {\n        grid-column: 2;\n        text-align: right;\n        padding-right: calc(1rem + 2vw);\n    }\n\n    .temp-scale-div {\n        grid-column: 1;\n        grid-row: 1;\n        padding-top: 5rem;\n        padding-left: calc(2rem + 2vw);\n    }\n\n    .temp-scale-div .temp-div {\n        display: flex;\n        font-size: 60px;\n    }\n\n    .weather-div .icon {\n        width: 200px;\n        grid-column: span 2;\n        justify-self: center;\n    }\n\n    .condition-desc-div {\n        padding-top: 8px;\n    }\n\n    .condition-desc-div .condition {\n        font-size: 30px;\n    }\n\n    .condition-desc-div .description {\n        font-size: 22px;\n    }\n\n    .slides-div {\n        height: 200px;\n    }\n\n    .slides-div .weather-properties-div {\n        display: flex;\n        flex-wrap: wrap;\n        justify-content: center;\n    }\n\n    .weather-properties-div .square-div {\n        width: 150px;\n    }\n\n    .square-div .square-title {\n        font-size: 18px;\n    }\n\n    .daily-div {\n        margin-top: 1rem;\n        flex-direction: row;\n        overflow-y: none;\n        overflow-x: scroll;\n        scroll-snap-type: x mandatory;\n        scroll-behavior: smooth;\n        -webkit-overflow-scrolling: touch;\n    }\n\n    .daily-div::-webkit-scrollbar {\n        height: 5px;\n        width: 5px;\n    }\n}\n\n\n@media (width >=1200px) {\n    .weather-properties-div .square-div {\n        flex-direction: row;\n        width: 250px;\n        height: 75px;\n        align-items: center;\n        justify-content: space-around;\n    }\n\n    .square-content {\n        margin: 0;\n    }\n\n    .daily-div .day-div {\n        width: 150px;\n        height: 150px;\n        flex-direction: column;\n        justify-content: space-between;\n    }\n\n    .day-div .day-temp-div {\n        flex-direction: row;\n        justify-content: space-around;\n    }\n}\n\n@media (width >=1320px) {\n    .weather-properties-div .square-div {\n        width: 300px;\n    }\n\n    .daily-div {\n        justify-content: center;\n    }\n}"],"sourceRoot":""}]);
